body
    background: rgb(65,63,128)
    header
        width: 90%
        height: 10%
        display: flex
        justify-content: flex-end
        padding: 2em
        text-align: right
        input
            background: rgb(40, 36, 151)
            color: #fff
            padding: 1em
            border-radius: 10em
            outline: none
            border: none
            box-shadow: 0px 5px 5px #110f44
            &::placeholder
                color: #fff
    .movies
        display: flex
        flex-wrap: wrap
        width: 90%
        margin: 0 auto
        justify-content: space-between
        .movie_block
            display: flex
            flex-direction: column
            width: 190px
            color: #fff
            background: #110f44
            padding: 1em
            margin: 1em 1em
            .img-wrap
                width: 98%
                height: 220px
                margin: 0 auto
                img
                    width: 100%
                    height: 100%
            h3
                max-width: 100%
                margin: 0
                text-align: center
                a
                    text-decoration: none
                    color: #fff
                    font-size: 0.5em
                    &:hover
                        color: red
